













































import Vue from "vue";
import axios from "axios";
import OptionButton from "../components/OptionButton.vue";
import { Card, CardManager } from "../components/type/Card";

export default Vue.extend({
    data() {
        return {
            isWait: false,
            isImageLoad: false,
            score: 0,
            round: 0,
            quizTypes: ["image", "name", "text", "cost"],
            totalRound: this.$store.state.quizOption.totalRound,
            optionCnt: this.$store.state.quizOption.optionCnt,
            type: this.$store.state.quizOption.type,
            answerCard: {} as Card,
            answerIndex: 0 as number,
            optionCards: [] as Card[]
        };
    },
    components: {
        OptionButton
    },
    created() {
        this.startGame();
    },
    methods: {
        imageLoadClear(): void {
            this.isImageLoad = true;
        },
        startGame(): void {
            if (this.round < this.totalRound) {
                this.isImageLoad = false;
                this.round++;
                this.isWait = false;
                let cardManager = CardManager.getCardManager();

                this.answerIndex = cardManager.getRandom(this.optionCnt - 1);
                this.answerCard = cardManager.getRandomCard();
                this.optionCards = cardManager.getRandomCards(
                    this.optionCnt - 1
                );
            } else {
                this.$store.commit("setScore", this.score);
                this.$router.push("/clear");
            }
        },
        selectAnswer(index: number): void {
            if (this.isWait == false) {
                this.isWait = true;
                var roundBar: any = document.getElementById("roundBar");
                roundBar.style.width =
                    (this.round / this.totalRound) * 100 + "%";
                setTimeout(this.startGame, 500);
                if (index == this.answerIndex) {
                    this.score++;
                    var scoreBar: any = document.getElementById("scoreBar");
                    scoreBar.style.width =
                        (this.score / this.totalRound) * 100 + "%";
                } else {
                }
            }
        }
    },
    computed: {
        //TODO: 판정 오류가 있음
        getOptionListName(): Card[] {
            let output: Card[] = this.optionCards;
            output.splice(this.answerIndex, 0, this.answerCard);
            return output;
        },
        getOptionListCost(): number[] {
            let output: number[] = CardManager.getCardManager().getRandomCostExclusion(
                this.answerCard.cost
            );
            output.splice(this.answerIndex, 0, this.answerCard.cost);
            return output;
        }
    }
});
